import { Swiper, SwiperSlide } from "swiper/react";
import 'swiper/css';
import './Mainslider.css';
import { Autoplay} from "swiper/modules";

function Mainslider() {
  return (
    <div className="main-slider-wrap">
      <Swiper
        className="main-swiper"
        modules={[Autoplay]}
        loop={true}
        slidesPerView={1}
        autoplay={{delay: 14000, disableOnInteraction: false}}
      >
        {/* 1: () */}
        <SwiperSlide>
          <div className="main-slide-bg">
            <video src={process.env.PUBLIC_URL + "/video/main1.mp4"} autoPlay loop muted playsInline className="main-slide-media" />
          </div>
          <div className="main-slide-center slide-white">
            <h2>POCKET COLLECTION</h2>
            <div className="main-slide-btns">
              <button></button>
              <button> </button>
            </div>
          </div>
        </SwiperSlide>
        {/* 2: () */}
        <SwiperSlide>
          <div className="main-slide-bg">
            <img src={process.env.PUBLIC_URL +"/img/main2.avif"} className="main-slide-media" alt="slide2" />
          </div>
          <div className="main-slide-center slide-black">
            <h2>NEW IN</h2>
            <div className="main-slide-btns">
              <button></button>
              <button> </button>
            </div>
          </div>
        </SwiperSlide>
        {/* 3: () */}
        <SwiperSlide>
          <div className="main-slide-bg">
            <video src= {process.env.PUBLIC_URL +"/video/main3.mp4"} autoPlay loop muted playsInline className="main-slide-media" />
          </div>
          <div className="main-slide-center slide-white">
            <h2>2025 COLLECTION</h2>
            <div className="main-slide-btns">
              <button></button>
              <button> </button>
            </div>
          </div>
        </SwiperSlide>
        {/* 4: (, h2 , / ) */}
        <SwiperSlide>
          <div className="main-slide-bg">
            <img src={process.env.PUBLIC_URL +"/img/main4.avif"} className="main-slide-media" alt="slide4" />
          </div>
          <div className="main-slide-center slide-white slide4">
            <img src={process.env.PUBLIC_URL +"/img/main4title.avif"} alt="4 " className="h2-image" />
            <div className="main-slide-btns">
              <button></button>
              <button> </button>
            </div>
          </div>
        </SwiperSlide>
      </Swiper>
    </div>
  );
}

export default Mainslider;