import React, { useState } from "react";
import { Navbar, Nav, NavDropdown, Container } from "react-bootstrap";
import './Header.css';
import { Link, useNavigate } from "react-router-dom";

function Header() {
  const navigate = useNavigate()
  const [menuOpen, setMenuOpen] = useState(false);

  return (
    <>
      {/*        */}
      {menuOpen && (
        <div className="gentle-drawer-bg" onClick={() => setMenuOpen(false)}>
          <nav className="gentle-drawer" onClick={e => e.stopPropagation()}>
            <button className="drawer-close" onClick={() => setMenuOpen(false)}></button>
            <Link onClick={() => {navigate("/")}}></Link>
            <Link onClick={() => setMenuOpen(false)}></Link>
            <Link onClick={() => setMenuOpen(false)}></Link>
            <Link onClick={() => setMenuOpen(false)}></Link>
            <Link onClick={() => setMenuOpen(false)}></Link>
            <Link onClick={() => setMenuOpen(false)}> </Link>
          </nav>
        </div>
      )}

      <Navbar expand="lg" className="gentle-navbar">
        <Container fluid>
          {/*    */}
          <button
            className="gentle-hamburger"
            onClick={() => setMenuOpen(true)}
            aria-label=" "
          >
            <img src={process.env.PUBLIC_URL + "/img/menu.png"} alt="menu" />
          </button>
          {/*   */}
          <Nav className="me-auto align-items-center gentle-left-nav">
            <NavDropdown title="" id="nav-dropdown-sunglass">
              <NavDropdown.Item></NavDropdown.Item>
              <NavDropdown.Item> </NavDropdown.Item>
              <NavDropdown.Item></NavDropdown.Item>
              <NavDropdown.Item></NavDropdown.Item>
              <NavDropdown.Item>2025 </NavDropdown.Item>
              <NavDropdown.Item> </NavDropdown.Item>
            </NavDropdown>
            <NavDropdown title="" id="nav-dropdown-glasses">
              <NavDropdown.Item></NavDropdown.Item>
              <NavDropdown.Item> </NavDropdown.Item>
              <NavDropdown.Item></NavDropdown.Item>
            </NavDropdown>
            <Nav.Link></Nav.Link>
            <Nav.Link></Nav.Link>
            <Nav.Link> </Nav.Link>
          </Nav>

          {/*   */}
          <Navbar.Brand className="mx-auto gentle-logo" onClick={() => {navigate("/")}}>            
            <img src={process.env.PUBLIC_URL + "/img/logo.png"} alt="toplogo"></img>            
          </Navbar.Brand>

          {/*   */}
          <Nav className="ms-auto align-items-center gentle-right-nav">
            <Nav.Link>
              <img src={process.env.PUBLIC_URL + "/img/search.svg"} alt="search" className="gentle-icon" />
            </Nav.Link>
            <Nav.Link>
              <img src={process.env.PUBLIC_URL + "/img/user.svg"} alt="user" className="gentle-icon" />
            </Nav.Link>
            <Nav.Link as={Link} to="/cart">
              <img src={process.env.PUBLIC_URL + "/img/bag.svg"} alt="bag" className="gentle-icon" />
            </Nav.Link>
          </Nav>
        </Container>
      </Navbar>
    </>
  );
}

export default Header;